<template>

  <div id="app">

    <omnidb-header :pages="pages"></omnidb-header>

    <template v-for="page in pages">

      <template v-if="page.status">
        <template v-if="page.templateName == 'overview'">
          <omnidb-page-overview></omnidb-page-overview>
        </template>
      </template>

    </template>

    <div id="page-content">
      <div class="container">
        <h1>Testes</h1>
        <img src="./assets/logo.png">
        <router-view/>
      </div>
    </div>

  </div>

</template>

<script>
import OmnidbHeader from './components/omnidb-header.vue';

//Pages
import OmnidbPageOverview from './pages/omnidb-page-overview.vue';
import OmnidbPageDocumentation from './pages/omnidb-page-documentation.vue';

export default {
  name: 'App',
  components: {
    OmnidbHeader,
    OmnidbPageOverview,
    OmnidbPageDocumentation
  },
  inject:['EventBus'],
  data () {
    return {
      pages: [
        {
          url: '/',
          label: 'Overview',
          status: true,
          templateName: 'overview'
        },
        {
          url:  '/documentation',
          label: 'Documentation',
          status: false,
          templateName: 'documentation'
        }
      ]
    }
  },

  methods: {

    getPageTemplateByStatus() {
      let arr = this.pages;

      for (var i=0, iLen=arr.length; i<iLen; i++) {

        if (arr[i].status == true) return arr[i];
      }

    },

    getPageTemplateByUrl(value) {
      let arr = this.pages;

      for (var i=0, iLen=arr.length; i<iLen; i++) {

        if (arr[i].url == value) return arr[i];
      }

    },

    changePageContent(value) {

      let oldTemplate = this.getPageTemplateByStatus(),
          newTemplate = this.getPageTemplateByUrl(value);

      jQuery(oldTemplate).status == false;
      jQuery(newTemplate).status == true;



      console.log(oldTemplate, newTemplate);

    }

  },

  mounted() {
    let vueThis = this;

    this.EventBus.$on('omnidb:load-page', function(value) {
      //console.log(value)

      vueThis.changePageContent(value);

    });
  }
}
</script>

<style lang="sass">

  @import './assets/scss/style.scss'

</style>



